import { helpCreateEl } from './drawUI';
export const apiWinners = 'http://localhost:3000/winners';
let countWinners = 0;
let count = 0;
export async function fetchWinners(url) {
    try {
        const tableContain = document.querySelector('.win-container');
        const response = await fetch(url, { method: 'GET' });
        const arrWin = await response.json();
        arrWin.forEach((data) => {
            count += 1;
            const el = helpCreateEl('tr', `${data.id}`);
            const temp = `<td>${count}</td>
        <td class = "win__look">how car look</td>
        <td>car name</td>
        <td>${data.wins}</td>
        <td>${data.time}</td>`;
            tableContain === null || tableContain === void 0 ? void 0 : tableContain.append(el);
            el.innerHTML = temp;
        });
    }
    catch (error) {
        console.error('Error fetching winnersApi:', error.message);
    }
}
await fetchWinners(apiWinners);
export async function apiGetWinTotal(page, limit = 10) {
    try {
        const response = await fetch(`${apiWinners}?_page=${page}&_limit=${limit}`, { method: 'GET' });
        countWinners = Number(response.headers.get('X-Total-count'));
        const winsResp = await response.json();
        console.log(winsResp);
    }
    catch (error) {
        console.error('Error fetching winners total count:', error.message);
    }
    const winnersCountHtml = document.querySelector('.winners-count');
    if (winnersCountHtml)
        winnersCountHtml.innerHTML = `${countWinners}`;
}
apiGetWinTotal(1);
export async function apiGetWin(id) {
    try {
        const response = await fetch(`${apiWinners}/${id}`, { method: 'GET' });
        const winResp = await response.json();
        console.log(winResp);
    }
    catch (error) {
        console.error('Error fetching winner id:', error.message);
    }
}
export const apiWinnerDelete = async (id) => {
    try {
        await fetch(`${apiWinners}/${id}`, {
            method: 'DELETE',
        });
    }
    catch (error) {
        console.error('Error fetching car data:', error.message);
    }
};
