export const apiGarage = `http://localhost:3000/garage`;
let countCars = 0;
export const apiCarsPage = async (page, limit = 7) => {
    try {
        const response = await fetch(`${apiGarage}?_page=${page}&_limit=${limit}`, { method: 'GET' });
        countCars = Number(response.headers.get('X-Total-count'));
        const arrCarsPage = response.json();
        const garageCount = document.querySelector('.garage-count');
        if (garageCount)
            garageCount.innerHTML = `${countCars}`;
        console.log(arrCarsPage);
    }
    catch (error) {
        console.error('Error fetching car data:', error.message);
    }
};
export const apiCarDelete = async (id) => {
    try {
        await fetch(`${apiGarage}/${id}`, {
            method: 'DELETE',
        });
    }
    catch (error) {
        console.error('Error fetching car data:', error.message);
    }
};
export const apiGetCar = async (id) => (await fetch(`${apiGarage}/${id}`, { method: 'GET' })).json();
export const apiUpdateCar = async (body, id) => {
    try {
        await fetch(`${apiGarage}/${id}`, {
            method: 'PUT',
            body: JSON.stringify(body),
        });
    }
    catch (error) {
        console.error('Error fetching car data:', error.message);
    }
};
export const apiCreateCar = async (body) => {
    try {
        await fetch(apiGarage, {
            method: 'POST',
            body: JSON.stringify(body),
        });
    }
    catch (error) {
        console.error('Error fetching car data:', error.message);
    }
};
